<template>
    <div id="TablaComponent">

<div id="latabla" class="d-flex justify-content-center" >
    <table v-if="mostrarTabla" class="bg-primary">
            <tr>
                <th class="border border-black fw-bold">Curso</th>
                <th class="border border-black px-3">Cupos</th>
                <th class="border border-black px-3">Inscritos</th>
                <th class="border border-black px-3">Duracion</th>
                <th class="border border-black px-3">Costo</th>
                <th class="border border-black px-3">Terminado</th>
                <th class="border border-black px-3">Fecha</th>
                <th class="border border-black px-3">Acciones</th>
            </tr>

            <tr v-for="dato in datos_tabla" v-bind:key="dato.id">
                <td class="border border-black fw-bold">{{  dato.curso }}</td>
                <td class="border border-black">{{  dato.cupos }}</td>
                <td class="border border-black">{{  dato.inscritos }}</td>
                <td class="border border-black">{{  dato.duracion }}</td>
                <td class="border border-black">{{  dato.costo }}</td>
                <td class="border border-black">{{  dato.terminado }}</td>
                <td class="border border-black">{{  dato.fecha }}</td>
                <td class="border border-black"> <b-button variant="py-1 fs-7"  v-on:click.prevent="activarEditar(dato.id)" ><b-icon icon="pencil-square" class="text-warning"></b-icon></b-button> <b-button variant="py-1 fs-7" v-on:click.prevent="eliminar(dato.id)"><b-icon icon="trash-fill" class="text-danger"></b-icon></b-button> </td>
            </tr>

        </table>







                <!-- MODAL -->
                <div>
              <b-modal v-model="modalShow">
              
                <div>
                    <b-form @submit="onSubmit" @reset="onReset" v-if="show">
                      <b-form-group
                        id="input-group-1"
                        label="Nombre del Curso:"
                        label-for="input-1"
                        description="Ingrese el nombre del curso"
                      >
                        <b-form-input
                          id="input-1"
                          v-model="form.nombre"
                          type="text"
                          placeholder="Ingrese Nombre"
                          required
                        ></b-form-input>
                      </b-form-group>

                      <b-form-group 
                        id="input-group-2" 
                        label="URL Imagen del Curso:" 
                        label-for="input-2"
                        description="Ingrese url de imagen del curso">
                        <b-form-input
                          id="input-2"
                          v-model="form.img"
                          placeholder="Ingrese URL imagen"
                          required
                        ></b-form-input>
                      </b-form-group>

                      <b-form-group 
                        id="input-group-3" 
                        label="Cupos del Curso:" 
                        label-for="input-3"
                        description="Ingrese cantidad de cupos del curso">
                        <b-form-input
                          id="input-3"
                          type="number"
                          v-model="form.cupos"
                          placeholder="Ingrese cupos"
                          required
                        ></b-form-input>
                      </b-form-group>

                      <b-form-group 
                        id="input-group-4" 
                        label="Inscritos en el Curso:" 
                        label-for="input-4"
                        description="Ingrese la cantidad de inscritos en el curso">
                        <b-form-input
                          id="input-4"
                          type="number"
                          v-model="form.inscritos"
                          placeholder="Ingrese inscritos"
                          required
                        ></b-form-input>
                      </b-form-group>

                      <b-form-group 
                        id="input-group-5" 
                        label="Duración del Curso:" 
                        label-for="input-5"
                        description="Ingrese la duración del curso">
                        <b-form-input
                          id="input-5"
                          v-model="form.duracion"
                          placeholder="Ingrese duración"
                          required
                        ></b-form-input>
                      </b-form-group>

                      <b-form-group 
                        id="input-group-6" 
                        label="Fecha de Registro:" 
                        label-for="input-6"
                        description="Ingrese la fecha de registro del curso">
                        <b-form-input
                          id="input-6"
                          type="text"
                          v-model="form.fecha_registro"
                          placeholder="Ingrese fecha de registro"
                          required
                        ></b-form-input>
                      </b-form-group>


                      <b-form-group 
                        id="input-group-7" 
                        label="Costo del Curso:" 
                        label-for="input-7"
                        description="Ingrese el costo del curso">
                        <b-form-input
                          id="input-7"
                          type="number"
                          v-model="form.costo"
                          placeholder="Ingrese costo"
                          required
                        ></b-form-input>
                      </b-form-group>


                      <b-form-textarea
                        id="textarea"
                        v-model="form.descripcion"
                        placeholder="Descripción del Curso"
                        rows="3"
                        max-rows="6"
                      ></b-form-textarea>


                      <b-button type="submit" variant="warning">Cambiar Info</b-button>
                      <b-button type="reset" variant="danger">Reset</b-button>
                    </b-form>
                    <b-card class="mt-3" header="Form Data Result">
                      <pre class="m-0">{{ form }}</pre>
                    </b-card>
                </div>            
              
              
              
              </b-modal>
            </div>
<!-- FIN MODAL-->



</div>



    </div>
  </template>
  
  <script>
  import { mapState,mapActions } from 'vuex';
  export default {
    name: 'TablaComponent',
    props: {
    },
    data:function(){
      return{
        prueba: [
          { age: 40, first_name: 'Dickerson', last_name: 'Macdonald' },
          { age: 21, first_name: 'Larsen', last_name: 'Shaw' },
          { age: 89, first_name: 'Geneva', last_name: 'Wilson' },
          { age: 38, first_name: 'Jami', last_name: 'Carney' }
        ],
        modalShow: false,
        form: {
          id:'',
          nombre: '',
          img: '',
          costo: 0,
          duracion:'',
          cupos:0,
          inscritos:0,
          completado:false,
          fecha_registro:'',
          descripcion:'',
        },
        form_tabla: {
          id:'',
          curso: '',
          img: '',
          costo: 0,
          duracion:'',
          cupos:0,
          inscritos:0,
          terminado:false,
          fecha:'',
          descripcion:'',
        },
        show: true,
        mostrarTabla : true,
      }
    },
    methods:{
      ...mapActions('datos_cursos',['consultarDatosTabla', 'eliminarCurso', 'consultarCursosEditarCard', 'consultarCursosEditarTabla', 'editarDatosCard', 'editarDatosTabla']),
      eliminar:function(elid){
        console.log('elid',elid);
        let respuesta= confirm('¿Está seguro que desea eliminar el curso?');
        if(respuesta==true){
            this.eliminarCurso(elid);
        }
        
      },

      onSubmit(event) {
        event.preventDefault();

        this.$nextTick(() => {
          this.editarDatosCard(this.form);
        });

     
          this.form_tabla.id=this.form.id;
          this.form_tabla.curso=this.form.nombre;
          this.form_tabla.img=this.form.img;
          this.form_tabla.costo=this.form.costo;
          this.form_tabla.duracion=this.form.duracion;
          this.form_tabla.cupos=this.form.cupos
          this.form_tabla.inscritos=this.form.inscritos;
          this.form_tabla.terminado=this.form.completado;
          this.form_tabla.fecha=this.form.fecha_registro;
          this.form_tabla.descripcion=this.form.descripcion;



        this.$nextTick(() => {
          console.log('onSubmit form_tabla', this.form_tabla );
          this.editarDatosTabla(this.form_tabla);
        })

        this.$nextTick(() => {
          this.mostrarTabla= true
        })
        

      },
      onReset(event) {
        event.preventDefault()

          this.form.nombre='';
          this.form.img='',
          this.form.costo=0;
          this.form.duracion='';
          this.form.cupos=0;
          this.form.inscritos=0;
          this.form.completado=false;
          this.form.fecha_registro='';
          this.form.descripcion='';
   
        this.show = false
        this.$nextTick(() => {
          this.show = true
        })
      },

      activarEditar(unid){
        console.log('activareditar elid',unid);
   
        this.$nextTick(() => {
          this.mostrarTabla = false;
        })
   
        this.consultarCursosEditarCard(unid);
        this.consultarCursosEditarTabla(unid);
        this.modalShow = true;

        this.form.id = unid;

          this.form.nombre=this.curso_editar_card.nombre;
          this.form.img=this.curso_editar_card.img;
          this.form.costo=this.curso_editar_card.costo;
          this.form.duracion=this.curso_editar_card.duracion;
          this.form.cupos=this.curso_editar_card.cupos;
          this.form.inscritos=this.curso_editar_card.inscritos;
          this.form.completado=this.curso_editar_card.completado;
          this.form.fecha_registro=this.curso_editar_card.fecha_registro;
          this.form.descripcion=this.curso_editar_card.descripcion;




      },    

    },
    computed:{
      ...mapState('datos_cursos',['datos_tabla', 'curso_editar_card', 'curso_editar_tabla']),
    },
    watch:{},
    created(){

    }
  }
  </script>
  

  <style scoped>
  #TablaComponent{
    margin: 2%;
  }

  #latabla{
    text-align: center;
  }
  
  </style>
  